<template>
  <div id="app">
    <button @click="showNotification">Show Notification with Voice and Vibration</button>
  </div>
</template>

<script>
export default {
  name: "App",
  methods: {
    showNotification() {
      // 알림 권한 요청
      if (Notification.permission === "granted") {
        this.sendNotification();
      } else if (Notification.permission !== "denied") {
        Notification.requestPermission().then((permission) => {
          if (permission === "granted") {
            this.sendNotification();
          }
        });
      }
      
      // 음성 출력
      this.speak("Hello! This is a voice message with vibration from your PWA.");
      
      // 진동 기능 추가
      this.vibrate();
    },
    sendNotification() {
      // 알림 생성
      new Notification("Hello!", {
        body: "This is a notification message from your PWA.",
        icon: "path/to/icon.png", // 알림 아이콘 이미지 경로
      });
    },
    speak(text) {
      // SpeechSynthesis API로 음성 출력
      const speech = new SpeechSynthesisUtterance(text);
      speech.lang = "en-US"; // 언어 설정 (예: "ko-KR"로 변경하면 한국어 지원)
      window.speechSynthesis.speak(speech);
    },
    vibrate() {
      // Vibration API 사용 (모바일 기기에서 작동)
      if ("vibrate" in navigator) {
        navigator.vibrate([200, 100, 200]); // 진동 패턴 (200ms 진동, 100ms 대기, 다시 200ms 진동)
      }
    },
  },
};
</script>

<style>
button {
  font-size: 16px;
  padding: 10px 20px;
  background-color: #42b983;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}
</style>
